@import "../../Static/shared.css";
@import "forms.css";


.content_wrapper main {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
}

.form_wrapper h1 {
    font-weight: 900;
    margin-top: 0;
    width: auto;

    position: relative;
}

.form_wrapper h1:before {
    content: "";
    position: absolute;
    left: 0;
    bottom: -10px;
    width: 60px;
    height: 5px;
    background: white;
}

.form_wrapper button {
    margin: 0.5rem;
    padding: 1rem 2rem;
    border-radius: 2rem;
    font-weight: 900;
    font-size: 1.5rem;
    position: relative;

    height: 4rem;
    border: none;

    cursor:pointer;
    background: none;
}

.form_wrapper button:before {
    content: "";
    color: black;
    position: absolute;
    left: 1rem;
    top: 1rem;
    width: calc(100% - 2rem);
    height: calc(100% - 2rem);
    border-radius: 2rem;
    background: #0655ff;
    filter: blur(6px);
    transition: all .5s ease-in-out;
}

.form_wrapper button:hover:before {
    filter: blur(10px);
}

.form_wrapper button:active:before {
    filter: blur(16px);
}

.form_wrapper button:after {
    content: "Войти";
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    border-radius: 2rem;
    line-height: 3.5rem;
    background: rgba(0, 0, 0, 0.1);
    backdrop-filter: blur(10px);
    transition: all 0.5s ease-in-out;

    color: white;

    border: rgba(255, 255, 255, 0.3) 0.1rem solid;
    border-right: rgba(255, 255, 255, 0.15) 0.1rem solid;
    border-bottom: rgba(255, 255, 255, 0.15) 0.1rem solid;

    box-shadow: 0 0 5px 5px rgba(0, 0, 0, .3);
}

.form_wrapper button:hover:after {
    box-shadow: 0 0 5px 5px rgba(0, 0, 0, .5);
}

.form_wrapper button:active:after {
    box-shadow: 0 0 5px 5px rgba(0, 0, 0, 0);
}

.form_wrapper form p {
    font-size: 1.5rem;
    margin: .2rem;
    padding: 0;
}

#forgot {
    margin-top: 1rem;
}

.form_wrapper form p a {
    color: var(--light-blue);
}

.form_wrapper form .error {
    color: red;
    font-size: 1rem;
    margin: 0;
    padding: 0;
    text-align: left;
    width: 100%;
}

.form_wrapper canvas {
    width: 100%;
    max-width: 300px;

    margin: 0 10px;
}

/* Pattern for the background */
body::before {
    content: "";
    display: block;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 105vh;
    background-image: url("../../Static/Backgrounds/trianglify2.png");
    background-repeat: no-repeat;
    background-position: center center;
    filter: hue-rotate(72deg);
    background-size: cover;
    overflow: hidden;
    z-index: -100;
}